import React, {useState,useEffect} from 'react';
import Main from './Components/Main';
import Hourly from './Components/Hourly';
import Weekly from './Components/Weekly';
import Developer from './Components/Developer';
import './App.css';
import Loadingpage from './Components/Loadingpage';

function App() {

  const [userIp,setUserIp] = useState(0);
  const [userLat,setUserLat] = useState(0);
  const [userLong,setUserLong] = useState(0);
  const [weatherData,setWeatherData] = useState([]);
  const [cityName,setCityName] = useState('');
  const [currentDate,setCurrentDate] = useState('');
  const [country,setCountry] = useState('');
  const [updateView,setUpdateView] = useState(false);

  useEffect(()=>{
    getUserIp();
  },[userLong]);
  
  async function getUserIp(){
    const response = await fetch("https://geoip-db.com/json/");
    const ip = await response.json();
    setUserIp(ip.IPv4);
    getUserLocation(userIp);
    console.log('Event - 1');
  }

  async function getUserLocation(userIp){
    const response = await fetch(`https://ip-geolocation.whoisxmlapi.com/api/v1?apiKey=at_2n5ye7S6ZxfR1D7WmDxo8pwmaDJPW&ipAddress=${userIp}`);
    const data = await response.json();
    setUserLat(data.location.lat);
    setUserLong(data.location.lng);
    setCityName(data.location.city);
    const countryCode = data.location.country;
    getCountry(countryCode);
    getWeatherData(userLat,userLong);
    console.log('Event - 2');
  }

  async function getCountry(countryCode){
    const response = await fetch(`https://restcountries.eu/rest/v2/alpha/${countryCode}`);
    const data = await response.json();
    setCountry(data.name);
    console.log('Event - 3');
  }

  async function getWeatherData(userLat,userLong){
    if(userLat!==0&&userLong!==0){
      const response = await fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${userLat}&lon=${userLong}&units=metric&appid=7a8a7c8376d6bf111c3dcf7410c26c1e`);
      const data = await response.json();
      setWeatherData(data);
      getCurrentDate(data.current.dt);
    }
    console.log('Event - 4');
  }

  async function getCurrentDate(timestamp){
    const response = await fetch(`https://showcase.api.linx.twenty57.net/UnixTime/fromunixtimestamp?unixtimestamp=${timestamp}`);
    const data = await response.json();
    setCurrentDate(data);
    setUpdateView(true);
    console.log('Event - 5');
  }

  function weatherNameSetter(condition) {
    let weatherStateName = 'Stormy';
    if(condition>800){
      weatherStateName='Cloudy';
    }
    else if(condition>799){
      weatherStateName = 'Sunny';
    }
    else if(condition>700){
      weatherStateName = 'Foggy';
    }
    else if(condition>599){
      weatherStateName = 'Snowy';
    }
    else if(condition>299){
      weatherStateName = 'Rainy';
    }
    else {
      weatherStateName = 'Stormy';
    }

    return weatherStateName;
  }

  let toBeRendered = <Loadingpage />

  if(updateView){
    toBeRendered =
      <div className="App">
        <Main weatherData={weatherData} cityName={cityName} country={country} weatherNameSetter={weatherNameSetter}
        getWeatherData={getWeatherData}
        getCountry={getCountry}
        setCityName={setCityName}/>
        <Hourly weatherData={weatherData} currentDate={currentDate} weatherNameSetter={weatherNameSetter}/>
        <Weekly weatherData={weatherData} currentDate={currentDate} weatherNameSetter={weatherNameSetter}/>
        <Developer />
      </div>;
  }
  
  return(toBeRendered);
}

export default App;
