import React, {useState} from 'react';
import '../Styles/Searchbox.css';

function Searchbox({getWeatherData,getCountry,setCityName}) {

  const [searchActive, setSearchActive] = useState(false);
  const [responseStatus, setResponseStatus] = useState('empty');

  function changeSearchActivity(event) {
    if(!event.target.parentElement.parentElement.classList.contains('show')&&(event.type==='click'||event.type==='focus')){
      setSearchActive(true);
    }
    if(event.target.parentElement.parentElement.classList.contains('show')&&event.type!=='click'){
      setSearchActive(false);
      setResponseStatus('empty');
    }
  }

  function fetchCityWeather(event) {
    let searchWord = event.target.value;
    if(event.key==='Enter'){
      if(event.target.value=""){
        return;
      }
      setResponseStatus('Loading...');
      fetch(`https://api.openweathermap.org/data/2.5/weather?q=${searchWord}&appid=7a8a7c8376d6bf111c3dcf7410c26c1e`)
      .then(response=>{
        return response.json();
      })
      .then(data=>{
        if(data.cod==="404"){
          setResponseStatus('Location not found.');
          return;
        }
        getWeatherData(data.coord.lat,data.coord.lon).then(()=>{
          setCityName(data.name);
          getCountry(data.sys.country);
          setResponseStatus('Updated!');
          setTimeout(() => {
            setResponseStatus('empty');
          }, 1000);
        }
        );
      });
    }
    
  }

    return(
        <div className={`Searchbox ${searchActive ? 'show' : ''} ${responseStatus!=='empty' ? 'status-open' : ''}`}>
              <div className="searchbar">
                <input type="text" placeholder="Search a city"
                onClick={(event)=>changeSearchActivity(event)}
                onFocus={(event)=>changeSearchActivity(event)}
                onBlur={(event)=>changeSearchActivity(event)}
                onKeyDown={(event)=>fetchCityWeather(event)}/>
                <div className="search-icon">
                  <img src={require('../Assets/icons/search_icon.svg').default} alt=""/>
                </div>
              </div>
              <p className={`status-text ${responseStatus!=='empty' ? 'show' : ''}`}>{responseStatus}</p>
            </div>          
    );
}

export default Searchbox;