import React from 'react';
import '../Styles/Hourly.css';

import Hourcard from './Hourcard';

function Hourly ({weatherData,currentDate,weatherNameSetter}) {

    let currentTime = currentDate.Datetime;
    let hours = weatherData.hourly;
    let ch = Number(currentTime[11]+currentTime[12])+4; // ch is currentHour
    let cardList = Array.from(hours).filter((item,index)=>{
        return index>0 && index<13
    })

    return (
        <div className='Hourly'>
            <div className="hourly-forecast">Hourly Forecast</div>
            <div className="hours">
                {
                    cardList.map((item,index)=>{
                        if(ch>22){
                            ch-=24;  // When it is 23:00, next hour should be 00:00. This expression is for that purpose.
                        }
                        ch+=1;
                        let ch_placement = '';
                        if(ch<10){
                            ch_placement = `0${ch}` // When hour is a single number, zero is added in front of it.
                        }
                        else {
                            ch_placement = `${ch}`
                        }

                        let weatherStateName = weatherNameSetter(item.weather[0].id);
                        return <Hourcard key={index} hour={ch_placement} temp={Math.round(item.temp)} description={item.weather[0].main} weatherStateName={weatherStateName}/>
                    })
                }
            </div>
        </div>
    );
}

export default Hourly;