import React from 'react';
import '../Styles/Hourcard.css';

function Hourcard({hour,temp,description,weatherStateName}) {
    return(
        <div className="Hourcard">
            <p className="hour">{hour}<span className="zeros">00</span></p>
            <p className="weather-description">{description}</p>
            <p className="weather-temperature">{temp}°C</p>
            <div className="weather-icon"><img src={require(`../Assets/icons/${weatherStateName}_icon.svg`).default} alt=""/></div>
            
        </div>
    );
}

export default Hourcard;