import React from 'react';
import '../Styles/Weekcard.css';

function Weekcard ({theDate,temps,weatherStateName}) {
    return(
        <div className="Weekcard">
            <div className="date">
                <p className="month">{theDate.month}</p>
                <p className="day-num">{theDate.day}</p>
                <p className="day-name">{theDate.dayName}</p>
            </div>
            <div className="weather-icon">
                <img src={require(`../Assets/icons/${weatherStateName}_icon.svg`).default} alt=""/>
            </div>
            <div className="temperatures">
                <p className="day">D</p>
                <p className="day-temp">{Math.round(temps.day)} °C</p>
                <p className="night">N</p>
                <p className="night-temp">{Math.round(temps.night)} °C</p>
                <div className="line-horizontal"></div>
                <div className="line-vertical"></div>
                <div className="line-vertical"></div>
            </div>
        </div>
    );
}

export default Weekcard;