import React, {useEffect} from "react";
import '../Styles/Loadingpage.css';

function Loadingpage(params) {

useEffect(()=>{
    let theVariable = setInterval(() => {
        let allimages=document.querySelector('.Loadingpage').querySelectorAll('img');
        if(allimages!==undefined){
            iconSwiper(allimages);
            clearInterval(theVariable);
        }
    }, 1);
},[]);

function iconSwiper(allIcons) {
    for(let i=0;i<allIcons.length;i++){
        setTimeout(() => {
            i===4 ? allIcons[0].classList = '' : allIcons[i+1].classList = '';
            allIcons[i].classList.add('right');
            allIcons[i].classList.add('left');
        }, i*1500);
    }
}

    return(
        <div className="Loadingpage">
            <div className="images">
                <img src={require('../Assets/icons/Cloudy_icon.svg').default} alt=""/>
                <img src={require('../Assets/icons/Sunny_icon.svg').default} alt=""/>
                <img src={require('../Assets/icons/Rainy_icon.svg').default} alt=""/>
                <img src={require('../Assets/icons/Stormy_icon.svg').default} alt=""/>
                <img src={require('../Assets/icons/Snowy_icon.svg').default} alt=""/>
            </div>
            <p>Please Wait. The App is Loading.</p>
        </div>
    );
}

export default Loadingpage;